

















































































































import uniNavBar from '../../../components/uni-nav-bar.vue';
import uniSegmentedControl from '../../../components/uni-segmented-control.vue';
var pageCount = 10;
var rechargePage = 1,
	drawPage = 1,
	amountPage = 1,
	timer = null;
export default {
	data() {
		return {
			items: [
				'充值记录',
				'提现记录',
				'余额记录'
			],
			user_recharge_list: [],
			user_drawrequest_list: [],
			user_amount_list: [],
			current: 0,
			activeColor: '#fc3a39',
			styleType: 'text',
			showNull: false
		};
	},
	onLoad() {
		this.load(false);
	},
	computed: {
		money() {
			return this.$store.getters.getUserInfo.amount
		},
		list() {
			return this.current == 0 ? this.user_recharge_list : this.current == 1 ? this.user_drawrequest_list : this.user_amount_list
		},
		correctvalue(value) {
			return function(value) {
				return value > 0 ? "+" + value : value
			}
		}
	},
	components: {
		uniNavBar,
		uniSegmentedControl
	},
	onReachBottom: function() {
		var _self = this;
		if (timer != null) {
			clearTimeout(timer);
		}
		timer = setTimeout(function() {
			_self.load(true);
		}, 1000);
	},
	methods: {
		load(ismore) {
			switch (this.current) {
				case 0:
					this.loadRechargeList(ismore)
					break;
				case 1:
					this.loadDrawList(ismore)
					break;
				case 2:
					this.loadAmountList(ismore)
					break;
				default:
					break;
			}
		},
		loadRechargeList(ismore) {
			var that = this;
			if (ismore) {
				rechargePage++;
			} else {
				that.user_recharge_list = [];
			}
			this.$utils.tRequest({
				url: this.$webUrl + 'user_recharge_list',
				method: 'GET',
				data: {
					page: drawPage,
					pagesize: pageCount
				},
				success: res => {
					if (res.code == 0) {
						if (res.data.list.length > 0) {
							that.user_recharge_list = that.user_recharge_list.concat(res.data.list);
						} else {
							if (ismore) {
								rechargePage--
							}
						}

					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		loadDrawList(ismore) {
			var that = this;
			if (ismore) {
				drawPage++;
			} else {
				that.user_drawrequest_list = [];
			}
			this.$utils.tRequest({
				url: this.$webUrl + 'user_drawrequest_list',
				method: 'GET',
				data: {
					page: rechargePage,
					pagesize: pageCount
				},
				success: res => {
					if (res.code == 0) {
						if (res.data.list.length > 0) {
							that.user_drawrequest_list = that.user_drawrequest_list.concat(res.data.list);
						} else {
							if (ismore) {
								drawPage--
							}
						}
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		loadAmountList(ismore) {
			var that = this;
			if (ismore) {
				amountPage++;
			} else {
				that.user_amount_list = [];
			}
			this.$utils.tRequest({
				url: this.$webUrl + 'user_amount_list',
				method: 'GET',
				data: {
					page: amountPage,
					pagesize: pageCount
				},
				success: res => {
					if (res.code == 0) {
						if (res.data.list.length > 0) {
							that.user_amount_list = that.user_amount_list.concat(res.data.list);
						} else {
							if (ismore) {
								amountPage--
							}
						}
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		back() {
			uni.navigateBack({
				delta: 1
			})
		},
		onClickItem(index) {
			if (this.current !== index) {
				this.current = index;
				this.load()
			}
		},
		styleChange(evt) {
			const value = evt.target.value;
			if (this.styleType !== value) {
				this.styleType = value;
				for (let i = 0; i < this.styles.length; i++) {
					if (this.styles[i].value === value) {
						this.styleIndex = i;
						break;
					}
				}
			}
		}

	}

}
